.a{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    background: #ede7f6;
}
.b{
    position: relative;
   
    height: auto;
    padding: 50px 100px;
    background: #111;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.b .container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.b .container .sec.aboutus
{
    width: 40%;
}
.b  h1{
    position: relative;
    color: #fff;
    font-weight: 500;
    margin-bottom: 15px;
}
.b .container .sec.aboutus h2:before{
    content: '';
    position: absolute;
    bottom: -5px;
    left:0;
    width:50px;
    height: 2px;
    background: #f00;
}
.b p{
    color:#999;
    font-size:1.6rem;
}
.sci{
    margin-top: 20px;
    display: flex;
}
.sci li{
    list-style: none;
}
.sci li a{
    display: inline-block;
    width: 40px;
    height: 40px;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    text-decoration: none;
    border-radius: 4px;
}
.sci li a:hover{
    background: #f00;
}
.sci li a .fa{
    color: #fff;
    font-size:20px
}
.quicklinks{
    position: relative;
    width: 25%;
}
.quicklinks ul{
    padding-left: 0;
}
.quicklinks ul li {
    list-style: none;
}
.quicklinks ul li a{
    color: #999;
    text-decoration: none;
    margin-bottom: 10px;
    display: inline-block;
    font-size: 2rem;
}
.contact{
    width: calc(35%-60px);
    margin-right: 0 !important;
}
.contact .info{
    position: relative;
    padding-left: 0;
}
.contact .info li{
    display: flex;
    margin-bottom: 16px;
}
.contact .info li span:nth-child(1){
    color: #fff;
    font-size: 20px;
    margin-right: 10px;
}
.contact .info li span{
    color: #999;
}
.contact .info li a
{
    color: #999;
    font-size: 1.5rem;
    text-decoration: none;
}
.contact .info li a:hover{
    color: #fff;
}
@media(max-width:991px){
    .b{
        padding: 40px;
    }
    .b .container{
        flex-direction: column;
    }
    .b .container .sec{
        margin-right: 0;
        margin-bottom: 40px;
    }
    .b .container .sec.sec.aboutus,.quicklinks,.contact{
        width: 100%;
    }
}

